.header{
    display: flex;
    justify-content: space-between;
}

.logo{
    width: 10rem;
    height: 3rem;
}

.header-menu{
    list-style: none;
    display: flex;
    gap: 2rem;
    color: white;
}

.header-menu>li{
    cursor: pointer;
}

.header-menu>li:hover{
    color: #D23369;
    cursor: pointer;
}

@media screen and (max-width : 768px) {
    .header>:nth-child(2){
        position: fixed;
        right: 2rem;
        z-index: 99;
    }
    .header-menu{
        flex-direction: column;
        background-color: #9C254D;
        padding: 2rem;
    }
}